import React, { useState, useEffect } from "react";
import Carousel from "react-bootstrap/Carousel";
import axios from "axios";
import serverRequestPath from "../serverRequestPath";

export default function Slider() {
  const [images, setImages] = useState([]);
  useEffect(() => {
    axios.get(serverRequestPath + "/api/pictures/slider-home").then((res) => {
      setImages(res.data);
    });
  }, []);
  return (
    <>
      <Carousel controls={false}>
        {images.map((i) => {
          return (
            <Carousel.Item key={i}>
              <img
                style={{ width: "100%" }}
                src={`./images/slider-home/${i}`}
              />
            </Carousel.Item>
          );
        })}
      </Carousel>
    </>
  );
}
